:is(.flex-col, .flex) {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.grid {
  display: grid;
}

.uno-rem {
  gap: 1em;
}

body {
  background-color: var(--Dark-desaturated-cyan);
}

:is(header, main, section, footer) {
  width: min(90rem, 100% - 1rem);
  margin-inline: auto;
}

.header {
  display: none;
  align-items: center;
  justify-content: space-between;
  position: relative;
  height: 50px;
}
.header__logo {
  flex-basis: 9.375em;
  flex-grow: 1;
  align-items: center;
  gap: 0.375em;
}
.header__logo--image {
  width: 3.125em;
  border-radius: 50%;
}
.header__logo--letters {
  flex: 2;
  font-weight: 600;
}
.header__nav {
  display: none;
  flex-direction: column;
  position: absolute;
  inset: 60px 0 0 auto;
  height: 20rem;
  aspect-ratio: 3/3;
  background-color: white;
}
@media (min-width: 40em) {
  .header__nav {
    position: relative;
    inset: 0;
    height: auto;
    flex-direction: row;
    justify-content: flex-end;
    flex: 1;
    display: flex;
    aspect-ratio: 0;
    background-color: transparent;
  }
}
.header__links {
  flex: 1;
  text-align: center;
  color: var(--Very-dark-desaturated-blue);
}
@media (min-width: 40em) {
  .header__links {
    color: white;
  }
}
.header__toggle {
  flex-basis: 3.125em;
  background: transparent;
  border: 0.9px solid transparent;
  border-radius: 8px;
  cursor: pointer;
}
@media (min-width: 40em) {
  .header__toggle {
    display: none;
  }
}

.main {
  grid-template-areas: "one";
  place-items: center;
  align-items: center;
  background: var(--Soft-red);
}
.main__video {
  grid-area: one;
  object-fit: cover;
}
@media (min-width: 40em) {
  .main__video {
    aspect-ratio: 16/9;
  }
}
.main__h1 {
  grid-area: one;
  color: var(--White);
  font-size: clamp(1.75rem, 1rem + 5vw, 3rem);
  z-index: 2;
}

.about {
  --container-width: 400;
  background-color: var(--White);
  grid-template-rows: 1fr;
}
.about__services {
  flex-wrap: wrap;
}
.about__text {
  flex-basis: 25rem;
  flex-grow: 1;
  gap: 1em;
  justify-content: center;
  padding: 1.875em;
}
.about__image {
  flex-basis: 25rem;
  flex-grow: 1;
  width: 500px;
  aspect-ratio: 4/4;
  object-fit: cover;
}
.about__h2 {
  font-size: clamp(1.25rem, 1rem + 2.5vw, 2.25rem);
}
.about__p {
  color: var(--Dark-desaturated-cyan);
}
.about__link {
  color: var(--Very-dark-desaturated-blue);
  font-size: 1.25rem;
  font-weight: 800;
  width: fit-content;
  position: relative;
}
.about__link::after {
  content: "";
  position: absolute;
  inset: 20px 0 0 0;
  height: 5px;
  width: auto;
  opacity: 0.8;
  background-color: var(--Soft-red);
}
.about__music {
  flex-wrap: wrap;
}
.about__info {
  flex-basis: 25rem;
  flex-grow: 1;
  grid-template-areas: "video";
  place-items: center;
  position: relative;
  overflow: visible;
}
.about__text--video {
  grid-area: video;
  text-align: center;
  z-index: 2;
}
.about__h3--video {
  color: var(--White);
  font-size: 2rem;
}
.about__p--video {
  color: var(--White);
  font-weight: 600;
  font-size: 1.375rem;
  margin: 1em 0;
}
.about__btn {
  color: var(--White);
  padding: 1em 0.5em;
  border: 2px solid var(--White);
  border-radius: 16px;
  font-size: 1.375rem;
  z-index: 99;
}
.about__video {
  grid-area: video;
  aspect-ratio: 3/3;
  object-fit: cover;
}

.wrap-reverse {
  flex-wrap: wrap-reverse;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

img,
video,
svg,
iframe {
  display: block;
  max-width: 100%;
  width: 100%;
}

button {
  background: transparent;
  border: 0.5px solid transparent;
  cursor: pointer;
  text-transform: capitalize;
}

a,
:active {
  text-decoration: none;
  color: var(--Dark-blue);
  font-family: "Fraunces", serif;
}

h1,
h2,
h3,
h4 {
  font-family: "Fraunces", serif;
  color: var(--Very-dark-desaturated-blue);
}

p,
span {
  font-family: "Barlow", sans-serif;
  font-size: 1.125rem;
  line-height: 1.5;
}

:root {
  --Soft-red: hsl(7, 99%, 70%);
  --Yellow: hsl(51, 100%, 49%);
  --Dark-desaturated-cyan: hsl(167, 40%, 24%);
  --Dark-blue: hsl(198, 62%, 26%);
  --Dark-moderate-cyan: hsl(168, 34%, 41%);
}

:root {
  --Very-dark-desaturated-blue: hsl(212, 27%, 19%);
  --Very-dark-grayish-blue: hsl(213, 9%, 39%);
  --Dark-grayish-blue: hsl(232, 10%, 55%);
  --Grayish-blue: hsl(210, 4%, 67%);
  --White: hsl(0, 0%, 100%);
}

.show {
  display: flex;
  opacity: 1;
}

.fixed-nav {
  display: flex;
  position: fixed;
  inset: 0 0 auto 0;
  z-index: 9999;
  background: linear-gradient(var(--Dark-blue), rgba(224, 218, 218, 0));
  color: var(--White);
}

.music-video {
  position: absolute;
  inset: 20px;
  display: none;
}

.show-me-video {
  display: block;
}

.about__music--video {
  aspect-ratio: 4/4;
  object-fit: cover;
}